"use client";

import React from 'react';
import { useChannels } from '@/hooks/useChannels';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Separator } from '@/components/ui/separator';

export default function EPG() {
  const { selectedChannel, epgData } = useChannels();

  if (!selectedChannel) {
    return (
      <div className="p-6">
        <Card>
          <CardContent className="p-6 text-center">
            <div className="text-4xl mb-2">üìÖ</div>
            <p className="text-muted-foreground">Select a channel to view program guide</p>
          </CardContent>
        </Card>
      </div>
    );
  }

  return (
    <div className="p-6">
      <Card>
        <CardHeader className="pb-4">
          <CardTitle className="flex items-center gap-2">
            <span>Program Guide</span>
            <Badge variant="outline">{selectedChannel.name}</Badge>
          </CardTitle>
        </CardHeader>
        
        <CardContent className="space-y-4">
          {epgData && epgData.now ? (
            <div className="grid md:grid-cols-2 gap-6">
              {/* Now Playing */}
              <div className="space-y-3">
                <div className="flex items-center gap-2">
                  <div className="w-3 h-3 bg-red-500 rounded-full animate-pulse"></div>
                  <h3 className="font-semibold text-lg">Now Playing</h3>
                </div>
                
                <div className="bg-accent/50 rounded-lg p-4 border-l-4 border-red-500">
                  <h4 className="font-medium text-lg mb-1">{epgData.now.title}</h4>
                  <p className="text-sm text-muted-foreground mb-2">
                    {epgData.now.start} - {epgData.now.end}
                  </p>
                  {epgData.now.description && (
                    <p className="text-sm">{epgData.now.description}</p>
                  )}
                </div>
              </div>

              {/* Next Program */}
              {epgData.next && (
                <div className="space-y-3">
                  <div className="flex items-center gap-2">
                    <div className="w-3 h-3 bg-blue-500 rounded-full"></div>
                    <h3 className="font-semibold text-lg">Up Next</h3>
                  </div>
                  
                  <div className="bg-accent/30 rounded-lg p-4 border-l-4 border-blue-500">
                    <h4 className="font-medium text-lg mb-1">{epgData.next.title}</h4>
                    <p className="text-sm text-muted-foreground mb-2">
                      {epgData.next.start} - {epgData.next.end}
                    </p>
                    {epgData.next.description && (
                      <p className="text-sm">{epgData.next.description}</p>
                    )}
                  </div>
                </div>
              )}
            </div>
          ) : (
            <div className="text-center py-8">
              <div className="text-4xl mb-2">üì∫</div>
              <p className="text-muted-foreground">Program guide data unavailable</p>
              <p className="text-sm text-muted-foreground mt-1">
                EPG information will be displayed here when available
              </p>
            </div>
          )}

          <Separator className="my-4" />
          
          {/* Channel Info */}
          <div className="flex items-center justify-between text-sm">
            <div className="flex items-center gap-4">
              <span className="text-muted-foreground">Channel:</span>
              <Badge variant="secondary">{selectedChannel.category}</Badge>
            </div>
            
            <div className="text-muted-foreground">
              Last updated: {new Date().toLocaleTimeString()}
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
