"use client";

import React, { useRef, useState, useEffect } from 'react';
import { useChannels } from '@/hooks/useChannels';
import { Button } from '@/components/ui/button';
import { Slider } from '@/components/ui/slider';
import { Card, CardContent } from '@/components/ui/card';

export default function VideoPlayer() {
  const { selectedChannel } = useChannels();
  const videoRef = useRef<HTMLVideoElement>(null);
  const [playing, setPlaying] = useState(false);
  const [volume, setVolume] = useState([1]);
  const [currentTime, setCurrentTime] = useState(0);
  const [duration, setDuration] = useState(0);
  const [error, setError] = useState<string | null>(null);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    if (selectedChannel && videoRef.current) {
      setError(null);
      setLoading(true);
      videoRef.current.src = selectedChannel.streamUrl;
      videoRef.current.load();
    }
  }, [selectedChannel]);

  const handlePlay = async () => {
    if (videoRef.current) {
      try {
        await videoRef.current.play();
        setPlaying(true);
        setError(null);
      } catch (err) {
        setError("Failed to play video. Please try another channel.");
        setPlaying(false);
      }
    }
  };

  const handlePause = () => {
    if (videoRef.current) {
      videoRef.current.pause();
      setPlaying(false);
    }
  };

  const handleStop = () => {
    if (videoRef.current) {
      videoRef.current.pause();
      videoRef.current.currentTime = 0;
      setPlaying(false);
      setCurrentTime(0);
    }
  };

  const handleVolumeChange = (newVolume: number[]) => {
    setVolume(newVolume);
    if (videoRef.current) {
      videoRef.current.volume = newVolume[0];
    }
  };

  const handleTimeUpdate = () => {
    if (videoRef.current) {
      setCurrentTime(videoRef.current.currentTime);
    }
  };

  const handleLoadedMetadata = () => {
    if (videoRef.current) {
      setDuration(videoRef.current.duration);
      setLoading(false);
    }
  };

  const handleSeek = (newTime: number[]) => {
    if (videoRef.current) {
      videoRef.current.currentTime = newTime[0];
      setCurrentTime(newTime[0]);
    }
  };

  const formatTime = (time: number) => {
    const minutes = Math.floor(time / 60);
    const seconds = Math.floor(time % 60);
    return `${minutes.toString().padStart(2, '0')}:${seconds.toString().padStart(2, '0')}`;
  };

  if (!selectedChannel) {
    return (
      <Card className="h-full flex items-center justify-center">
        <CardContent className="text-center p-8">
          <div className="text-6xl mb-4">üì∫</div>
          <h2 className="text-2xl font-semibold mb-2">Welcome to IPTV Player Pro</h2>
          <p className="text-muted-foreground">Select a channel from the sidebar to start watching</p>
        </CardContent>
      </Card>
    );
  }

  return (
    <Card className="h-full flex flex-col">
      <CardContent className="flex-1 p-6">
        <div className="flex flex-col h-full">
          <div className="flex-1 bg-black rounded-lg overflow-hidden relative mb-4">
            {loading && (
              <div className="absolute inset-0 flex items-center justify-center bg-black/50 z-10">
                <div className="text-white">Loading...</div>
              </div>
            )}
            
            {error ? (
              <div className="absolute inset-0 flex items-center justify-center bg-black text-white">
                <div className="text-center">
                  <div className="text-4xl mb-4">‚ö†Ô∏è</div>
                  <p className="text-lg mb-2">Video Error</p>
                  <p className="text-sm text-gray-300">{error}</p>
                </div>
              </div>
            ) : (
              <video
                ref={videoRef}
                className="w-full h-full object-contain"
                onTimeUpdate={handleTimeUpdate}
                onLoadedMetadata={handleLoadedMetadata}
                onError={() => setError("Unable to load video stream")}
                onLoadStart={() => setLoading(true)}
                onCanPlay={() => setLoading(false)}
                controls={false}
                preload="metadata"
              />
            )}
          </div>

          <div className="space-y-4">
            <div className="flex items-center justify-between">
              <h3 className="text-lg font-semibold">{selectedChannel.name}</h3>
              <div className="text-sm text-muted-foreground">
                {formatTime(currentTime)} / {formatTime(duration)}
              </div>
            </div>

            {/* Progress Bar */}
            <div className="space-y-2">
              <Slider
                value={[currentTime]}
                max={duration || 100}
                step={1}
                onValueChange={handleSeek}
                className="w-full"
                disabled={!duration}
              />
            </div>

            {/* Controls */}
            <div className="flex items-center justify-between">
              <div className="flex items-center space-x-2">
                <Button
                  onClick={playing ? handlePause : handlePlay}
                  variant="default"
                  size="sm"
                  disabled={!selectedChannel || loading}
                >
                  {playing ? 'Pause' : 'Play'}
                </Button>
                
                <Button
                  onClick={handleStop}
                  variant="outline"
                  size="sm"
                  disabled={!selectedChannel}
                >
                  Stop
                </Button>
              </div>

              <div className="flex items-center space-x-2">
                <span className="text-sm text-muted-foreground">Volume</span>
                <div className="w-24">
                  <Slider
                    value={volume}
                    max={1}
                    step={0.1}
                    onValueChange={handleVolumeChange}
                  />
                </div>
                <span className="text-sm text-muted-foreground w-8">
                  {Math.round(volume[0] * 100)}%
                </span>
              </div>
            </div>
          </div>
        </div>
      </CardContent>
    </Card>
  );
}
