import { useState, useEffect } from 'react';

export interface Channel {
  id: number;
  name: string;
  category: string;
  streamUrl: string;
  logo?: string;
}

interface EPGProgram {
  title: string;
  start: string;
  end: string;
  description?: string;
}

interface EPGData {
  now?: EPGProgram;
  next?: EPGProgram;
}

const demoChannels: Channel[] = [
  { id: 1, name: "Global News HD", category: "News", streamUrl: "https://sample-videos.com/zip/10/mp4/SampleVideo_1280x720_1mb.mp4" },
  { id: 2, name: "Sports Center", category: "Sports", streamUrl: "https://sample-videos.com/zip/10/mp4/SampleVideo_1280x720_2mb.mp4" },
  { id: 3, name: "Movie Classics", category: "Movies", streamUrl: "https://sample-videos.com/zip/10/mp4/SampleVideo_1280x720_5mb.mp4" },
  { id: 4, name: "Discovery World", category: "Documentary", streamUrl: "https://sample-videos.com/zip/10/mp4/SampleVideo_1280x720_1mb.mp4" },
  { id: 5, name: "Music TV", category: "Music", streamUrl: "https://sample-videos.com/zip/10/mp4/SampleVideo_1280x720_2mb.mp4" },
  { id: 6, name: "Kids Zone", category: "Kids", streamUrl: "https://sample-videos.com/zip/10/mp4/SampleVideo_1280x720_5mb.mp4" },
  { id: 7, name: "Tech Today", category: "Technology", streamUrl: "https://sample-videos.com/zip/10/mp4/SampleVideo_1280x720_1mb.mp4" },
  { id: 8, name: "Food Network", category: "Lifestyle", streamUrl: "https://sample-videos.com/zip/10/mp4/SampleVideo_1280x720_2mb.mp4" },
];

const generateEPGData = (channelName: string): EPGData => {
  const currentHour = new Date().getHours();
  const nextHour = currentHour + 1;
  
  const programs = {
    "Global News HD": [
      { title: "Breaking News", description: "Latest world updates" },
      { title: "Weather Report", description: "Today's forecast" }
    ],
    "Sports Center": [
      { title: "Live Football", description: "Premier League match" },
      { title: "Sports Highlights", description: "Best moments recap" }
    ],
    "Movie Classics": [
      { title: "Classic Cinema", description: "Timeless masterpiece" },
      { title: "Movie Reviews", description: "Critics corner" }
    ],
    "Discovery World": [
      { title: "Nature Documentary", description: "Wildlife exploration" },
      { title: "Science Explained", description: "Educational content" }
    ],
    "Music TV": [
      { title: "Top Hits", description: "Latest music videos" },
      { title: "Live Concert", description: "Artist performance" }
    ],
    "Kids Zone": [
      { title: "Cartoon Time", description: "Fun animations" },
      { title: "Educational Fun", description: "Learning through play" }
    ],
    "Tech Today": [
      { title: "Tech News", description: "Latest innovations" },
      { title: "Product Reviews", description: "Gadget analysis" }
    ],
    "Food Network": [
      { title: "Cooking Show", description: "Master chef recipes" },
      { title: "Food Travel", description: "Culinary adventures" }
    ]
  };

  const channelPrograms = programs[channelName as keyof typeof programs] || [
    { title: "Live Programming", description: "Current show" },
    { title: "Next Show", description: "Upcoming content" }
  ];

  return {
    now: {
      title: channelPrograms[0].title,
      start: `${currentHour.toString().padStart(2, '0')}:00`,
      end: `${nextHour.toString().padStart(2, '0')}:00`,
      description: channelPrograms[0].description
    },
    next: {
      title: channelPrograms[1].title,
      start: `${nextHour.toString().padStart(2, '0')}:00`,
      end: `${(nextHour + 1).toString().padStart(2, '0')}:00`,
      description: channelPrograms[1].description
    }
  };
};

export function useChannels() {
  const [allChannels] = useState<Channel[]>(demoChannels);
  const [channels, setChannels] = useState<Channel[]>(demoChannels);
  const [selectedChannel, setSelectedChannel] = useState<Channel | null>(null);
  const [searchTerm, setSearchTerm] = useState('');
  const [epgData, setEpgData] = useState<EPGData>({});
  const [favorites, setFavorites] = useState<number[]>([]);

  const selectChannel = (channel: Channel) => {
    setSelectedChannel(channel);
    setEpgData(generateEPGData(channel.name));
  };

  const searchChannels = (query: string) => {
    setSearchTerm(query);
    if (query.trim() === "") {
      setChannels(allChannels);
    } else {
      const filtered = allChannels.filter((channel) =>
        channel.name.toLowerCase().includes(query.toLowerCase()) ||
        channel.category.toLowerCase().includes(query.toLowerCase())
      );
      setChannels(filtered);
    }
  };

  const toggleFavorite = (channelId: number) => {
    setFavorites(prev => 
      prev.includes(channelId) 
        ? prev.filter(id => id !== channelId)
        : [...prev, channelId]
    );
  };

  const getChannelsByCategory = (category: string) => {
    return allChannels.filter(channel => channel.category === category);
  };

  const getCategories = () => {
    return Array.from(new Set(allChannels.map(channel => channel.category)));
  };

  return { 
    channels, 
    selectedChannel, 
    selectChannel, 
    searchChannels, 
    epgData, 
    favorites,
    toggleFavorite,
    getChannelsByCategory,
    getCategories,
    searchTerm
  };
}
