"use client";

import React from 'react';
import SearchBar from './SearchBar';
import { useChannels } from '@/hooks/useChannels';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { ScrollArea } from '@/components/ui/scroll-area';

export default function ChannelList() {
  const { channels, selectChannel, selectedChannel, favorites, toggleFavorite } = useChannels();

  return (
    <div className="flex flex-col h-full">
      <div className="mb-4">
        <SearchBar />
      </div>
      
      <ScrollArea className="flex-1">
        {channels.length === 0 ? (
          <div className="p-4 text-center text-muted-foreground">
            No channels found
          </div>
        ) : (
          <div className="space-y-2">
            {channels.map((channel) => (
              <div
                key={channel.id}
                className={`group relative p-3 rounded-lg border transition-all duration-200 cursor-pointer hover:shadow-md ${
                  selectedChannel?.id === channel.id 
                    ? 'bg-primary text-primary-foreground border-primary shadow-md' 
                    : 'bg-card text-card-foreground border-border hover:bg-accent hover:text-accent-foreground'
                }`}
                onClick={() => selectChannel(channel)}
              >
                <div className="flex items-center justify-between">
                  <div className="flex-1 min-w-0">
                    <h3 className="font-medium truncate">{channel.name}</h3>
                    <div className="flex items-center gap-2 mt-1">
                      <Badge 
                        variant="secondary" 
                        className={`text-xs ${
                          selectedChannel?.id === channel.id 
                            ? 'bg-primary-foreground/20 text-primary-foreground' 
                            : ''
                        }`}
                      >
                        {channel.category}
                      </Badge>
                    </div>
                  </div>
                  
                  <Button
                    variant="ghost"
                    size="sm"
                    className={`ml-2 opacity-0 group-hover:opacity-100 transition-opacity ${
                      favorites.includes(channel.id) ? 'opacity-100' : ''
                    }`}
                    onClick={(e) => {
                      e.stopPropagation();
                      toggleFavorite(channel.id);
                    }}
                  >
                    <span className={`text-lg ${
                      favorites.includes(channel.id) ? 'text-yellow-500' : 'text-muted-foreground'
                    }`}>
                      ‚òÖ
                    </span>
                  </Button>
                </div>
                
                {selectedChannel?.id === channel.id && (
                  <div className="absolute left-0 top-0 bottom-0 w-1 bg-primary-foreground rounded-r"></div>
                )}
              </div>
            ))}
          </div>
        )}
      </ScrollArea>
      
      <div className="mt-4 pt-4 border-t border-border">
        <div className="text-xs text-muted-foreground text-center">
          {channels.length} channels available
        </div>
      </div>
    </div>
  );
}
